import { Typography } from "@rp/react/web/mui/core"
import { BaseChip } from "./BaseChip"

type UnsupportedChipProps = {
  onRemove?: () => void
}

const UnsupportedChip = ({ onRemove }: UnsupportedChipProps) => (
  <BaseChip onRemove={onRemove}>
    <Typography>Unsupported</Typography>
  </BaseChip>
)

export { UnsupportedChip }
export type { UnsupportedChipProps }
